import { LitElement, css, html } from 'lit';
import { customElement, property } from 'lit/decorators.js';
import { DateTime } from 'luxon';

import { getLocalizedMonth } from '../lib/localization.js';
import { getMessages } from '../lib/messages.js';
import type { ViewMode } from '../lib/ViewStateController.js';

@customElement('lms-calendar-header')
export default class Header extends LitElement {
    @property({ type: String })
    heading?: string;

    @property({ type: Object })
    activeDate?: CalendarDate;

    @property({ type: String })
    viewMode: ViewMode = 'month';

    @property({ type: Object })
    expandedDate?: CalendarDate;

    @property({ type: String })
    locale = 'en';

    static override styles = css`
        :host {
            display: block;
            container-type: inline-size;
        }

        .controls {
            height: var(--header-height, 3.5em);
            width: 100%;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            align-content: center;
            justify-content: space-between;
            align-items: center;
            border-bottom: 1px solid var(--separator-light);
        }

        .info {
            padding-left: var(--header-info-padding-left, 1em);
            text-align: right;
            display: grid;
        }

        @container (max-width: 600px) {
            .controls {
                font-size: small;
                height: auto;
                flex-wrap: wrap;
                justify-content: center;
                padding: 0.5em 0;
                gap: 0.35em;
            }
            .info {
                width: 100%;
                text-align: center;
                padding-left: 0;
            }
            .context,
            .buttons {
                flex-shrink: 0;
            }
            .buttons {
                padding-right: 0;
            }
        }

        .view-detail {
            grid-row: 2;
            grid-column: 1;
            visibility: hidden;
        }

        .view-detail.active {
            visibility: visible;
        }
        .day,
        .week,
        .month,
        .year {
            color: var(--header-text-color, rgba(0, 0, 0, 0.6));
        }
        button {
            padding: 0.4em 0.7em;
            margin: 0;
            border: none;
            background: none;
            font: inherit;
            font-size: 0.85em;
            color: var(--header-text-color, rgba(0, 0, 0, 0.6));
            cursor: pointer;
            border-radius: var(--border-radius-sm, 5px);
            transition: background-color 0.15s ease, color 0.15s ease;
        }

        button:hover {
            background: var(--separator-light, rgba(0, 0, 0, 0.1));
        }

        .context {
            display: flex;
            align-items: center;
            gap: 0;
            background: var(--separator-light, rgba(0, 0, 0, 0.06));
            border-radius: var(--border-radius-sm, 5px);
            padding: 0.15em;
        }

        .context button {
            border-radius: calc(var(--border-radius-sm, 5px) - 1px);
        }

        .context button[data-active] {
            background: var(--background-color, white);
            color: var(--separator-dark, rgba(0, 0, 0, 0.7));
            box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
        }

        .buttons {
            display: flex;
            align-items: center;
            padding-right: var(--header-buttons-padding-right, 1em);
            gap: 0.15em;
        }

        .buttons .separator {
            width: 1px;
            height: 1.2em;
            background: var(--separator-light, rgba(0, 0, 0, 0.15));
            margin: 0 0.2em;
        }
    `;

    private _getWeekInfo(date: CalendarDate) {
        const dt = DateTime.fromObject({
            year: date.year,
            month: date.month,
            day: date.day,
        });

        return {
            weekNumber: dt.weekNumber,
            weekYear: dt.weekYear,
        };
    }

    override render() {
        const date = this.activeDate!;
        const msg = getMessages(this.locale);

        return html`<div class="controls">
            <div class="info">
                <span>
                    <strong>${this.heading || msg.currentMonth}</strong>
                </span>
                <div class="view-detail${this.viewMode === 'day' ? ' active' : ''}">
                    <span class="day">${date.day}</span>
                    <span class="month"
                        >${getLocalizedMonth(date.month, this.locale)}</span
                    >
                    <span class="year">${date.year}</span>
                </div>
                <div class="view-detail${this.viewMode === 'week' ? ' active' : ''}">
                    <span class="week"
                        >${msg.calendarWeek}
                        ${this._getWeekInfo(date).weekNumber}</span
                    >
                    <span class="month"
                        >${getLocalizedMonth(date.month, this.locale)}</span
                    >
                    <span class="year"
                        >${this._getWeekInfo(date).weekYear}</span
                    >
                </div>
                <div class="view-detail${this.viewMode === 'month' ? ' active' : ''}">
                    <span class="month"
                        >${getLocalizedMonth(date.month, this.locale)}</span
                    >
                    <span class="year">${date.year}</span>
                </div>
            </div>
            <nav class="context" aria-label="Calendar view" @click=${this._dispatchSwitchView}>
                <button
                    type="button"
                    ?data-active=${this.viewMode === 'day'}
                    aria-pressed=${this.viewMode === 'day' ? 'true' : 'false'}
                    data-context="day"
                    class="btn-change-view"
                >
                    ${msg.day}
                </button>
                <button
                    type="button"
                    ?data-active=${this.viewMode === 'week'}
                    aria-pressed=${this.viewMode === 'week' ? 'true' : 'false'}
                    data-context="week"
                    class="btn-change-view"
                >
                    ${msg.week}
                </button>
                <button
                    type="button"
                    ?data-active=${this.viewMode === 'month'}
                    aria-pressed=${this.viewMode === 'month' ? 'true' : 'false'}
                    data-context="month"
                    class="btn-change-view"
                >
                    ${msg.month}
                </button>
            </nav>
            <div class="buttons" @click=${this._dispatchSwitchDate}>
                <button type="button" name="previous" aria-label="Previous">«</button>
                <button type="button" name="next" aria-label="Next">»</button>
                <span class="separator"></span>
                <button type="button" name="today" @click=${this._handleTodayClick}>
                    ${msg.today}
                </button>
            </div>
        </div>`;
    }

    private _handleTodayClick(e: Event) {
        e.stopPropagation(); // Prevent triggering _dispatchSwitchDate

        const today = new Date();
        const todayDate = {
            day: today.getDate(),
            month: today.getMonth() + 1,
            year: today.getFullYear(),
        };

        const event = new CustomEvent('jumptoday', {
            detail: { date: todayDate },
            bubbles: true,
            composed: true,
        });
        this.dispatchEvent(event);
    }

    private _dispatchSwitchDate(e: Event) {
        const target = e.target;
        if (!(target instanceof HTMLButtonElement)) {
            return;
        }

        const direction = e.target === e.currentTarget ? 'container' : target.name;
        const event = new CustomEvent('switchdate', {
            detail: { direction },
            bubbles: true,
            composed: true,
        });
        this.dispatchEvent(event);
    }

    private _dispatchSwitchView(e: Event) {
        const target = e.target;
        if (!(target instanceof HTMLElement)) {
            return;
        }

        const view = e.target === e.currentTarget ? 'container' : target.dataset.context;
        const event = new CustomEvent('switchview', {
            detail: { view },
            bubbles: true,
            composed: true,
        });
        this.dispatchEvent(event);
    }
}
